import React from 'react';
import { List, Avatar } from 'antd';
import { Row, Col } from 'antd';

const companyHistory = [
  {
    title: '',
    img:'../gcp.jpg',
    desc:'2016.01.25 ~  (5+)',
    content:' ',
  },
  {
    title: 'NHN ',
    img:'../nhn.jpg',
    desc:'2012.08.16 ~ 2016.01.05 (3)',
    content:'  ',
  },
  {
    title: '',
    img:'../com2us.jpg',
    desc:'2009.07.06 ~ 2011.08.17 (2)',
    content:' ',
  },
  {
    title: '',
    img:'../bluenet.jpg',
    desc:'2004.01.09 ~ 2006.11.08 (3)',
    content:'  ()',
  },
];

const scholarHistory = [
  {
    title: '',
    img:'../hanyang.jpg',
    desc:'2001.03 ~ 2009.02',
    content:'   ',
  },
  {
    title: '',
    img:'../yangjung.jpg',
    desc:'1998.01 ~ 2001.02',
    content:'',
  },
]

const History = () => {
  return (
      <>
      <Row gutter={[20,20]} justify="space-between">
      <Col xs={24} sm={24} md={12} lg={12}>
        <h2 className="titleSeparate2"></h2>
        <List
          itemLayout="horizontal"
          dataSource={companyHistory}
          renderItem={item => (
            <List.Item>
              <List.Item.Meta
                avatar={<Avatar src={item.img} size={64}/>}
                title={item.title}
                description={item.desc}
              />
              <div>{item.content}</div>
            </List.Item>
          )}
        />
      </Col>
      <Col xs={24} sm={24} md={12} lg={12}>
        <h2 className="titleSeparate2"></h2>
        <List
          itemLayout="horizontal"
          dataSource={scholarHistory}
          renderItem={item => (
            <List.Item>
              <List.Item.Meta
                avatar={<Avatar src={item.img} size={64}/>}
                title={item.title}
                description={item.desc}
              />
              <div>{item.content}</div>
            </List.Item>
          )}
        />
      </Col>    
      </Row>  
    </>
  );
};
export default History;
