import React from 'react';
import { Row, Col } from 'antd';
import AboutTile from '../../AbouTile';
import { stripTags, domHtml } from '../../../utils/stripTags';

import SEO from '../../Seo';

const pageText = {
  paraOne: ` <b> </b>   . <br/>
  <b>  </b>  <b>DW</b>  <b>ETL </b>    .<br/>
    <b>Python, SQL, ShellScript</b>  ,   <b>Java, Go</b>  .<br/>
  <br/>
      ,  .<br/>
    ,              .<br/>
    `,
  paraTwo: `     Javascript(React), Unity,   Kubernetes, MLOps .<br/>
    react  gatsby    .  
  `,
};


const AboutMe = () => {
  const description = `${pageText.paraOne} ${stripTags(pageText.paraTwo)}`;
  return (
      <>
      <div>
        <SEO
          title="About"
          description={description}
          path=""
          keywords={['Rolwin', 'Reevan', 'Monteiro', 'FullStack developer', 'Javascript', 'ReactJS', 'NodeJS', 'Gatsby']}
        />
        <h1 className="titleSeparate">About Me</h1>
        <p dangerouslySetInnerHTML={domHtml(pageText.paraOne)}/>
        <p dangerouslySetInnerHTML={domHtml(pageText.paraTwo)} />
      </div>
      
      <Row gutter={[20, 20]}>
        <Col xs={24} sm={24} md={12} lg={8}>
          <AboutTile
            img="dev-python.jpg"
            height={60}
            alt="Python image"
            textH4="   .<br/>,,      ."
            textH3="Python"
          />
        </Col>
        <Col xs={24} sm={24} md={12} lg={8}>
          <AboutTile
            img="dev-java.jpg"
            alt="coffee image"
            textH4="   Java   .   Springboot + JPA  "
            textH3="Java"
          />
        </Col>
        <Col xs={24} sm={24} md={12} lg={8}>
          <AboutTile
            img="dev-go.jpg"
            alt="go image"
            textH4="      .      ."
            textH3="Golang"
          />
        </Col>
        <Col xs={24} sm={24} md={12} lg={8}>
          <AboutTile
            img="dev-fluentd.jpg"
            alt="fluentd image"
            textH4="ETL  Fluentd       ."
            textH3="Fluentd"
          />
        </Col>
        <Col xs={24} sm={24} md={12} lg={8}>
          <AboutTile
            img="dev-kafka.jpg"
            alt="Kafka"
            textH4=" ETL   Kafka        ."
            textH3="Kafka"
            height={60}
            width={60}
          />
        </Col>
        <Col xs={24} sm={24} md={12} lg={8}>
          <AboutTile
            img="dev-bigquery.jpg"
            alt="Bigquery"
            textH4="SQL  .    ,,      ."
            textH3="Google Cloud Bigquery"
            height={60}
            width={60}
          />
        </Col>
      </Row>           
    </>
  );
};
export default AboutMe;
